import React from 'react';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import Image from '../elements/Image';

const propTypes = {
  ...SectionProps.types
}

const defaultProps = {
  ...SectionProps.defaults
}

const WorktermThreeContent = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container">
        <div className={innerClasses}>
          <div className="center-image-text">
            <div className="reveal-from-bottom" data-reveal-delay="200">
              <Image
                className="round-image"
                src={require('./../../assets/images/job3.png')}
                alt="Features tile icon 01"
                width={64}
                height={64}
              />
            </div>
            <h1 className="pl-16 mt-0 mb-16 reveal-from-bottom" data-reveal-delay="200">
              CloudOps
            </h1>
          </div>
          <div className="text-xxs text-color-primary fw-600 tt-u mb-8 center-content reveal-from-bottom" data-reveal-delay="300">
            Full-stack Developer
          </div>

          <div className="container mb-40">
            <h3 className="mt-8 mb-12 reveal-from-bottom" data-reveal-delay="400">
              A Quick Summary
            </h3>
            <p className="m-0 reveal-from-bottom" data-reveal-delay="400">
              I spent my third work term at CloudOps, a Canadian company based in Montreal that provides cloud consulting, managed services, and software focused on open source, cloud-native platforms, networking, and DevOps. Something cool that I didn't know until recently, is that the CloudOps logo visually depicts the path to value for customers. The C and the O of the logo are combined to create a cloud with the top of the C presenting an on-ramp into cloud adoption. Moving into the O, the logo represents an iterative cycle of continuous improvement, adaptation, and evolution as requirements are constantly updated! During my time here, I participated in the Full-stack development of a tool that provides public and private multi-cloud management services through work on various bug fixes, refactors, and features, including the application's implementation of the AWS Java SDK that gives users the ability to manage AWS EKS Kubernetes clusters through the application! Adding on to that, I actively participated in daily stand-up meetings, backlog maintenance sessions, sprint retrospectives, and even presented in a product stakeholder demo, and in a company-wide update for the products team!     
            </p>
          </div>
          <div className="container mb-40">
            <h3 className="mt-8 mb-12 reveal-from-bottom" data-reveal-delay="400">
              What I Improved on and Learned About
            </h3>
            <p className="m-0 reveal-from-bottom" data-reveal-delay="400">
              As a software developer at CloudOps, I recognize that I've learned more than I ever thought I could in such a short time! The product I worked on was a full stack application, and the codebase was hosted on Github, and I had to work concurrently on the same branches and stories with another intern daily. CIS3760 was a really helpful course when it came to this since in that course I collaborated with several other students to create a full stack application through git. CIS2430 was also a super helpful course since I got to learn about Java, the main language that is used at CloudOps, and object-oriented practices, which the company codebase very commonly implemented. On top of that, the courses CIS1050, CIS2750, and CIS3760 also involved implementing various front-end features, which assisted with my front-end development at CloudOps. VueJS was something I was happy that I got exposure to since it's a very widely used front-end framework, as well as more exposure to Jest. The main thing I'm glad I got exposure to was Java though, since my last internship was solely front-end based, I felt like my back-end development skills were lagging. I learned so many neat tips and tricks about Java, such as proper use of streams and completable futures, as well as interacting with the AWS Java SDK, which required a lot of reading through the documentation, something that I previously never spent that much time doing. I also finally took the time to learn how to use the Java debugger to its fullest, and it has been more useful than I could've ever imagined! Other than that, I got more practice with Jira, especially creating new issues, opening pull requests through Github, performing code walkthroughs, code reviews, concurrent work on a single branch, and resolving merge conflicts.            
            </p>
          </div>

          <div className="container mb-40 ">
            <h3 className="mt-8 mb-12 reveal-from-bottom" data-reveal-delay="400">
              My 3 Learning Goals
            </h3>
            <p className="m-0 mb-32 reveal-from-bottom" data-reveal-delay="400">
              Before starting work, I set 3 goals to develop during my work term, as dictated by my Co-op program.
            </p>
            <ol>
              <div className="mb-4 reveal-from-bottom" data-reveal-delay="400"><h5 className="m-0"><li>I want to improve my backend skills</li></h5><p>I can say with complete certainty that I've considerably developed my back-end skills during my time at CloudOps. I got to spend time working on around 30 back-end-oriented bug fixes and features. The language that was used was Java, but Java is a pretty complicated language, before my internship I didn't know anything about streams and completable futures, and my object-oriented programming skills were pretty weak, however now, I am much more comfortable with all of those concepts! I also got to work with various API fetchers, attribute mappers, view builders, and caching classes. I even got the privilege of creating some of those classes from scratch, which are currently being used in production for the application's implementation of Amazon's Elastic Kubernetes service. On top of that, I got to work with unit testing through the use of Spock, and Spring Boot and its various annotations, such as @Autowired and @Component. I didn't get too much of a chance to work with SQL, but the depth of knowledge regarding Java 100% makes up for it.</p></div>
              <div className="mb-4 reveal-from-bottom" data-reveal-delay="400"><h5 className="m-0"><li>I want to improve my understanding of various cloud technologies</li></h5><p>While I was at CloudOps, I spent around half of the time working on the application's AWS implementation, more specifically the Amazon Elastic Kubernetes services (EKS). I learned in great depth about how they're structured, and their requirements. To first create an EKS cluster, there must be a virtual private cloud (VPC), along with 2 subnetworks that belong to that VPC which are in two different regions (to ensure that latency is still low if congestion occurs). After this, an EKS cluster can be created. Only then can you create a node group for that cluster, where you can specify how many nodes you'd want it to have. Fun fact, an EKS node is just an Amazon Elastic Compute Cloud (EC2) instance with a few extra functionalities and some extra meta-data. Once you create a node group, you'll be able to access the various pods and interact with the Kubernetes API through the AWS endpoint using a certificate authority key, and cluster access token key. Since I was able to explain this, I believe that it would be fair to mark this learning goal as completed!</p></div>
              <div className="mb-4 reveal-from-bottom" data-reveal-delay="400"><h5 className="m-0"><li>I want to better understand the quirks and functionalities of IntelliJ</li></h5><p>My final learning goal was to become more comfortable with, and better understand IntelliJ. Before working at CloudOps, I had never used an IDE. I would always just use commands directly from the console, which was always taught as a best practice at my university. However, I learned that leveraging IntelliJ to its fullest would save a boatload of time! The autocomplete for classes, methods, attributes, and auto importing of packages was super useful. Also, being able to run or debug the program, or install maven dependencies with the click of a button was convenient. The main thing that I'm glad I learned about though, is how to use the debugger to its fullest, before this I would always write print statements, and then have to wait for the project to rebuild, which sometimes took over a minute. Using the debugger I would no longer needlessly wait for the project to rebuild since I could stop the program at a breakpoint specified by me at a certain line during program runtime. Overall, I think that I learned a considerable amount about IntelliJ's features and that I now have many more tools for software development under my belt.</p></div>
            </ol>
          </div>

          <div className="container mb-40">
            <h3 className="mt-8 mb-12 reveal-from-bottom" data-reveal-delay="400">
              Conclusion
            </h3>
            <p className="m-0 reveal-from-bottom" data-reveal-delay="400">
              I'm extremely satisfied with everything I've learned during my time at CloudOps and know that I've fulfilled all of the goals that were set by me at the beginning of my work term. I gained experience with Java, Spock, Vue.JS, and Jest, while also getting exposure to AWS EC2, VPCs, Subnets, and Kubernetes, all skills that will be invaluable for me moving forward. I'm glad that I got to use Jira and Github again, especially dealing with merge conflicts, and experiencing concurrent work with multiple people on the same feature. I now feel like I've balanced my skillset a bit more back towards being equally proficient in front-end, and back-end development, and that I've further qualified myself to be an efficient software developer! It's been a great time here, thank you, Simon, Joel, Evan, Pat Hassan, Leslie, Alain, and An Khang for guiding me, and helping me improve my software development skills! 
            </p>
          </div>
        </div>
      </div>
    </section>
  );
}

WorktermThreeContent.propTypes = propTypes;
WorktermThreeContent.defaultProps = defaultProps;

export default WorktermThreeContent;